.form {
  display: flex;
  flex-direction: column;

  &__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;

    @media (min-width: 768px) {
      flex-direction: row;
      margin-bottom: 31px;
    }
  }

  &__input {
    font-family: $basic-font;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: $color-background;
    width: 280px;
    padding: 12.5px 0 12.5px 10px;
    background-color: $color-form;
    border: 1px $color-white solid;
    border-radius: 3px;
    cursor: pointer;
    margin-bottom: 20px;

    &::placeholder {
      font-family: $basic-font;
      font-weight: 400;
      font-size: 18px;
      line-height: 22px;
      color: $color-background;
      opacity: 1;
    }

    @media (min-width: 768px) {
      margin-left: 30px;
      width: 370px;
      margin-bottom: 0;
    }
  }

  &__text {
    position: relative;

    &::before {
      position: absolute;
      content: "";
      background-image: url(../images/file.svg);
      background-repeat: no-repeat;
      top: -2px;
      left: -25px;
      width: 17px;
      height: 20px;
    }
  }

  &__button {
    font-family: $basic-font;
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
    color: $color-background;
    text-transform: uppercase;
    width: 280px;
    text-align: center;
    padding: 12.5px 0 12.5px 0;
    border: 2px $color-blue solid;
    border-radius: 3px;
    background-color: $color-white;
    margin-bottom: 25px;
    cursor: pointer;

    &:hover {
      color: rgba(255, 255, 255, 1);
      background-color: rgba(255, 255, 255, 0.3);
      transition: all 0.3s ease;
    }

    &:active {
      opacity: 0.5;
      transition: opacity 0.3s ease;
    }

    @media (min-width: 768px) {
      width: 48%;
      margin-bottom: 0;
    }
  }

  &__submit {
    display: flex;
    align-self: center;
    justify-content: space-around;
    font-family: $basic-font;
    font-weight: 600;
    font-size: 15px;
    line-height: 18px;
    color: $color-white;
    background-color: $color-blue;
    text-transform: uppercase;
    border: 1px $color-white solid;
    border-radius: 3px;
    transition: all 0.3s ease;
    padding: 13px 0 13px 0;
    min-width: 280px;
    cursor: pointer;

    &:hover {
      color: rgba(255, 255, 255, 1);
      background-color: rgba(62, 156, 220, 0.5);
      transition: all 0.3s ease;
    }

    &:active {
      opacity: 0.3;
    }

    @media (min-width: 768px) {
      min-width: 370px;
      margin-top: 55px;
    }
  }

}

.select-menu {
  position: relative;
  width: 280px;
  margin-bottom: 20px;

  &__input {
    position: relative;
    font-family: $basic-font;
    font-size: 17px;
    line-height: 22px;
    color: $color-black;
    border: 1px $color-form solid;
    border-radius: 3px;
    padding: 12.5px 20px 12.5px 10px;
    display: flex;
    align-items: center;
    width: 100%;
    background-color: $color-form;
    cursor: pointer;

    @media (min-width: 775px) {
      font-size: 18px;
    }

    &::before {
      position: absolute;
      content: "";
      background-image: url(../images/down.svg);
      width: 20px;
      height: 20px;
      top: 50%;
      right: 4px;
      transform: translateY(-50%);
      background-repeat: no-repeat;
    }

    &:hover {
      color: rgba(255, 255, 255, 1);
      background-color: rgba(255, 255, 255, 0.3);
    }
  }
  &__dropdown {
    display: none;
    position: absolute;
    width: 100%;

    border-radius: 3px;
    max-height: 180px;
  }

  &--active {
    .select-menu__dropdown {
      display: block;
      border: 1px $color-border-select solid;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      border-top: none;
    }

    .select-menu__input {
      color: $color-white;
      background-color: $color-select;
      border: 1px $color-border-select solid;
      border-bottom: none;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;

      &::before {
        position: absolute;
        content: "";
        background-image: url(../images/up.svg);
        width: 20px;
        height: 20px;
        top: 50%;
        right: 4px;
        transform: translateY(-50%);
        background-repeat: no-repeat;
      }
    }
  }

  @media (min-width: 768px) {
    width: 370px;
    margin-bottom: 0;
  }

  &__list {
    background-color: $color-background;
    max-height: 180px;
    overflow-y: auto;
    top: 48px;
    left: 0;
    right: 0;
    text-align: left;
    scrollbar-width: thin;
    scrollbar-color: $color-blue $color-border-select;

    &::-webkit-scrollbar {
      width: 7px;
      border-radius: 3px;
      background-color: $color-border-select;
    }

    &::-webkit-scrollbar-thumb {
      background-color: $color-blue;
      border-radius: 3px;
    }
  }

  &__item {
    font-family: $basic-font;
    font-style: normal;
    font-weight: 400;
    font-size: 17px;
    line-height: 22px;
    color: $color-white;
    padding-left: 10px;
    padding-top: 15px;
    padding-bottom: 15px;

    @media (min-width: 775px) {
      font-size: 18px;
    }

    &:hover {
      color: $color-blue;
      cursor: pointer;
    }

    &:active {
      color: $color-blue;
      opacity: 0.5;
      transition: all 0.3s ease;
    }
  }
}

.range {
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;

  @media (min-width: 768px) {
    margin-right: 30px;
    margin-bottom: 0;
    width: 100%;
  }

  &__info {
    display: flex;
    justify-content: space-between;
  }

  &__description {
    font-family: $basic-font;
    font-weight: 400;
    font-size: 12px;
    line-height: 22px;
    color: $color-white;
    margin-right: 15px;

    @media (min-width: 768px) {
      font-size: 18px;
    }
  }

  &__number {
    font-family: $basic-font;
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    color: $color-form;

    @media (min-width: 768px) {
      font-size: 18px;
    }
  }

  &__input {
    -webkit-appearance: none;
    width: 100%;
    height: 6px;
    border: 1px $color-form solid;
    background-color: $color-slidercontainer;
    border-radius: 3px;
    -webkit-transition: 0.2s;
    transition: opacity 0.2s;
    margin-top: 16px;

    &:hover {
      opacity: 1;
    }

    &::-webkit-slider-thumb {
      -webkit-appearance: none;
      appearance: none;
      width: 26px;
      height: 26px;
      border: 2px $color-slider-border solid;
      border-radius: 50%;
      background-color: $color-blue;
      cursor: pointer;
    }

    &::-moz-range-thumb {
      width: 26px;
      height: 26px;
      border: 2px $color-slider-border solid;
      border-radius: 50%;
      background-color: $color-blue;
      cursor: pointer;
    }
  }
}
